import { Box, Button, CircularProgress, Paper, Typography, useMediaQuery, useTheme } from '@mui/material'
import WarningIcon from '@mui/icons-material/Warning'
import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

function Confirm() {
  const { enqueueSnackbar } = useSnackbar()
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  const { id: userId } = useParams()
  const [isConfirmed, setIsConfirmed] = useState<boolean>()

  useEffect(() => {
    axios
      .get<AxiosResponse>(`${process.env.REACT_APP_APIHOST}/auth/confirm/${userId}`)
      .then(() => {
        setIsConfirmed(true)
      })
      .catch(error => {
        setIsConfirmed(false)
        enqueueSnackbar(error.response?.data?.mesage ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요.', {
          variant: 'error'
        })
      })
  }, [])

  return (
    <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      <Helmet>
        <title>이메일 인증</title>
      </Helmet>
      <Paper
        square={!matches}
        sx={{
          my: theme.spacing(13),
          padding: 3,
          flex: 1,
          maxWidth: '360px',
          height: { xs: '90vh', sm: 'inherit' },
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          사용자 이메일 인증
        </Typography>
        <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          {isConfirmed === undefined && <CircularProgress />}
          {isConfirmed && (
            <Button href="/auth/login" variant="contained">
              로그인 페이지로
            </Button>
          )}
          {isConfirmed !== undefined && !isConfirmed && <WarningIcon fontSize="large" color="warning" />}
        </Box>
      </Paper>
    </Box>
  )
}

export default Confirm
