import { useRoutes } from 'react-router-dom'
import router from 'src/router'

import AdapterDateFns from '@mui/lab/AdapterDateFns'
import LocalizationProvider from '@mui/lab/LocalizationProvider'

import { CssBaseline, useMediaQuery, useTheme } from '@mui/material'
import { SnackbarProvider } from 'notistack'
import { AuthProvider, createRefresh } from 'react-auth-kit'
import ThemeProvider from './theme/ThemeProvider'
import axios from 'axios'

function App() {
  const content = useRoutes(router)
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('md'))

  const refreshAPI = createRefresh({
    interval: 5,
    refreshApiCallback: ({ refreshToken }) => {
      return axios
        .put(`${process.env.REACT_APP_APIHOST}/auth/refreshAccessToken`, {
          refreshToken
        })
        .then(res => ({
          isSuccess: true,
          newAuthToken: res.data?.accessToken,
          newAuthTokenExpireIn: Math.ceil(parseInt(res.data?.expiresIn) / 60),
          newRefreshToken: res.data?.refreshToken,
          newRefreshTokenExpiresIn: Math.ceil(parseInt(res.data?.refreshTokenExpiresIn) / 60)
        }))
        .catch(() => ({ isSuccess: false, newAuthToken: null }))
    }
  })

  return (
    <ThemeProvider>
      <LocalizationProvider dateAdapter={AdapterDateFns}>
        <SnackbarProvider maxSnack={3} dense={!matches} anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }}>
          <AuthProvider authType="localstorage" authName={'_auth'} refresh={refreshAPI}>
            <CssBaseline />
            {content}
          </AuthProvider>
        </SnackbarProvider>
      </LocalizationProvider>
    </ThemeProvider>
  )
}
export default App
