import axios from 'axios'
import { useSnackbar } from 'notistack'
import { lazy, Suspense, useEffect, useState } from 'react'
import { useAuthHeader, useIsAuthenticated } from 'react-auth-kit'
import { Navigate, Outlet, RouteObject, useParams } from 'react-router'
import SuspenseLoader from './components/SuspenseLoader'
import Confirm from './content/authentication/Confirm'
import BaseLayout from './layouts/BaseLayout'
import SidebarLayout from './layouts/SidebarLayout'

const Loader = Component => props =>
  (
    <Suspense fallback={<SuspenseLoader />}>
      <Component {...props} />
    </Suspense>
  )

interface RequireAuthProps {
  loginPath: string
  children?: JSX.Element
}
function RequireAuth({ loginPath, children }: RequireAuthProps) {
  const isAuthenticated = useIsAuthenticated()
  if (isAuthenticated()) return children ?? <Outlet />
  return <Navigate to={loginPath} />
}

interface RequireAdminProps {
  redirectPath?: string
  children?: JSX.Element
}
function RequireAdmin({ redirectPath, children }: RequireAdminProps) {
  const [isLoading, setIsLoading] = useState(true)
  const [isAdmin, setIsAdmin] = useState(null)
  const [message, setMessage] = useState(null)
  const { enqueueSnackbar } = useSnackbar()
  const params = useParams()
  const authHeader = useAuthHeader()
  useEffect(() => {
    axios
      .get<boolean>(`${process.env.REACT_APP_APIHOST}/auth/isAdmin`, {
        params: { clubId: params.id },
        headers: { Authorization: authHeader() }
      })
      .then(res => {
        setIsAdmin(res.data)
        if (!res.data) setMessage('해당 동아리의 운영진이 아닙니다.')
      })
      .catch(err => setMessage(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요'))
      .finally(() => setIsLoading(false))
  }, [])
  useEffect(() => {
    if (message) enqueueSnackbar(message, { variant: 'error' })
  }, [message])

  if (isLoading) return null
  if (isAdmin) return children ?? <Outlet />
  return <Navigate to={redirectPath ?? isAdmin === null ? '/' : `/club/${params.id}`} replace />
}

// Index
const Index = Loader(lazy(() => import('src/content/index/Index')))

// Users
const Login = Loader(lazy(() => import('src/content/authentication/Login')))
const Register = Loader(lazy(() => import('src/content/authentication/Register')))
const Mypage = Loader(lazy(() => import('src/content/users/Mypage')))

// Clubs
const Apply = Loader(lazy(() => import('src/content/club/Apply')))
const ClubIntro = Loader(lazy(() => import('src/content/club/Intro')))
const Profile = Loader(lazy(() => import('src/content/club/Profile')))
const Search = Loader(lazy(() => import('src/content/club/Search')))

// Manage
const Attendance = Loader(lazy(() => import('src/content/manage/Attendance')))
const AttendanceCheck = Loader(lazy(() => import('src/content/manage/Attendance/AttendanceCheck')))
const ConferenceRegister = Loader(lazy(() => import('src/content/manage/Conference/ConferenceRegister')))
const Form = Loader(lazy(() => import('src/content/manage/Form')))
const Intro = Loader(lazy(() => import('src/content/manage/Intro')))
const Member = Loader(lazy(() => import('src/content/manage/Member')))
const Migration = Loader(lazy(() => import('src/content/manage/Member/MigrationPage')))
const ApplyManage = Loader(lazy(() => import('src/content/manage/Apply')))

// Status
const Status404 = Loader(lazy(() => import('src/content/status/Status404')))
const Status500 = Loader(lazy(() => import('src/content/status/Status500')))

// Policy
const PrivacyPolicyPage = Loader(lazy(() => import('src/content/policy')))

const routes: RouteObject[] = [
  {
    path: '',
    element: <SidebarLayout />,
    children: [
      {
        path: '',
        element: <Index />
      },
      {
        path: 'search',
        element: <Search />
      },
      {
        path: 'auth',
        children: [
          {
            path: 'login',
            element: <Login />
          },
          {
            path: 'register',
            element: <Register />
          },
          {
            path: 'confirm/:id',
            element: <Confirm />
          }
        ]
      },
      {
        path: 'club/:id',
        element: <ClubIntro />
      },{
        path: 'privacy-policy',
        element: <PrivacyPolicyPage />
      },
      {
        path: '',
        element: <RequireAuth loginPath="/auth/login" />,
        children: [
          {
            path: 'Mypage',
            element: <Mypage />
          },
          {
            path: 'club/:id',
            children: [
              {
                path: 'apply',
                element: <Apply />
              },
              {
                path: 'profile',
                element: <Profile />
              }
            ]
          },
          {
            path: 'manage/:id',
            element: <RequireAdmin />,
            children: [
              {
                path: 'attendance',
                element: <Attendance />
              },
              {
                path: 'attendance/:conferenceId',
                element: <AttendanceCheck />
              },
              {
                path: 'attendance/register',
                element: <ConferenceRegister />
              },
              {
                path: 'form',
                element: <Form />
              },
              {
                path: 'intro',
                element: <Intro />
              },
              {
                path: 'member',
                element: <Member />
              },
              {
                path: 'member/migration',
                element: <Migration />
              },
              {
                path: 'member/applicants',
                element: <ApplyManage />
              }
            ]
          }
        ]
      }
    ]
  },

  {
    path: 'status',
    element: <BaseLayout />,
    children: [
      {
        path: '',
        element: <Navigate to="404" replace />
      },
      {
        path: '404',
        element: <Status404 />
      },
      {
        path: '500',
        element: <Status500 />
      }
    ]
  },
  {
    path: '*',
    element: <Navigate to="/status/404" replace />
  }
]

export default routes
