import ChevronLeftIcon from '@mui/icons-material/ChevronLeft'
import ChevronRightIcon from '@mui/icons-material/ChevronRight'
import GroupsRoundedIcon from '@mui/icons-material/GroupsRounded'
import HomeRoundedIcon from '@mui/icons-material/HomeRounded'
import MenuIcon from '@mui/icons-material/Menu'
import PeopleOutlineRoundedIcon from '@mui/icons-material/PeopleOutlineRounded'
import PeopleRoundedIcon from '@mui/icons-material/PeopleRounded'
import SearchIcon from '@mui/icons-material/Search'
import LockRoundedIcon from '@mui/icons-material/LockRounded'
import {
  Avatar,
  CSSObject,
  Divider,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  styled,
  Theme,
  useMediaQuery,
  useTheme
} from '@mui/material'
import MuiDrawer from '@mui/material/Drawer'
import { useState } from 'react'

import { useAuthUser } from 'react-auth-kit'
import { stringToColor } from 'src/utils'
import { useLocation, Location } from 'react-router'

const openedMixin = (theme: Theme): CSSObject => ({
  width: theme.sidebar.width,
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen
  }),
  overflowX: 'hidden'
})

const closedMixin = (theme: Theme): CSSObject => ({
  transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen
  }),
  overflowX: 'hidden',
  width: `calc(${theme.spacing(6)} + 1px)`,
  [theme.breakpoints.up('lg')]: {
    width: `calc(${theme.spacing(8)} + 1px)`
  }
})

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(0, 1),
  height: theme.header.height,
  // necessary for content to be below app bar
  ...theme.mixins.toolbar
}))

const Drawer = styled(MuiDrawer, { shouldForwardProp: prop => prop !== 'open' })(({ theme, open }) => ({
  width: theme.sidebar.width,
  flexShrink: 0,
  whiteSpace: 'nowrap',
  boxSizing: 'border-box',
  ...(open && {
    ...openedMixin(theme),
    '& .MuiDrawer-paper': openedMixin(theme)
  }),
  ...(!open && {
    ...closedMixin(theme),
    '& .MuiDrawer-paper': closedMixin(theme)
  })
}))

const LockBadge = styled(LockRoundedIcon)(() => ({
  position: 'absolute',
  top: '55%',
  left: '55%',
  width: '16px',
  height: '16px'
}))

interface DrawerItemProps {
  open: boolean
  text: string
  icon: any
  badge?: any
  href: string
  location: Location
  theme: Theme
}

const DrawerItem = ({ open, text, icon, badge, href, location, theme }: DrawerItemProps) => {
  const [pathname, search] = href.split('?', 2)
  let matches = false
  if (pathname === location?.pathname) {
    if (search && location?.search.includes(search)) matches = true
    if (!search && !location?.search) matches = true
  }
  return (
    <ListItem key={text} disablePadding sx={{ display: 'block' }}>
      <ListItemButton
        href={href}
        selected={matches}
        sx={{
          minHeight: 48,
          justifyContent: open ? 'initial' : 'center',
          px: { base: 1, lg: 2.5 }
        }}
      >
        <ListItemIcon
          sx={{
            position: 'sticky',
            minWidth: 0,
            mr: open ? 3 : 'auto',
            justifyContent: 'center',
            color: matches ? theme.colors.primary.dark : 'default'
          }}
        >
          {icon}
          {badge}
        </ListItemIcon>
        <ListItemText
          primary={text}
          primaryTypographyProps={{ color: matches ? theme.colors.primary.dark : 'default' }}
          sx={{
            opacity: open ? 1 : 0,
            textOverflow: 'ellipsis',
            overflow: 'hidden',
            whiteSpace: 'nowrap'
          }}
        />
      </ListItemButton>
    </ListItem>
  )
}

interface ClubAvatarProps {
  name: string
}

const ClubAvatar = ({ name }: ClubAvatarProps) => (
  <Avatar
    children={name[0]}
    sx={{
      width: '32px',
      height: '32px',
      color: stringToColor(name, 0.3, 0.6),
      backgroundColor: stringToColor(name, 0.3)
    }}
  />
)

function Sidebar() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('lg'))
  const [open, setOpen] = useState(matches ? true : false)
  const authUser = useAuthUser()
  const clubs = authUser()?.clubs
  const adminClubs = clubs?.filter(c => c.isAdmin)
  const location = useLocation()

  const handleDrawerClick = () => setOpen(!open)

  return (
    <Drawer
      variant={'permanent'}
      open={open}
      sx={{ position: matches ? 'relative' : 'fixed', zIndex: matches ? 5 : 10 }}
    >
      <DrawerHeader sx={{ justifyContent: open ? 'flex-end' : 'center' }}>
        <IconButton onClick={handleDrawerClick}>
          {open ? theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon /> : <MenuIcon />}
        </IconButton>
      </DrawerHeader>
      <Divider />
      <List>
        <DrawerItem open={open} text="KLUB" icon={<HomeRoundedIcon />} href="/" location={location} theme={theme} />
        <DrawerItem
          open={open}
          text="동아리 검색"
          icon={<SearchIcon />}
          href="/search"
          location={location}
          theme={theme}
        />
      </List>
      <Divider />
      <List>
        <DrawerItem
          open={open}
          text="연합 동아리"
          icon={<GroupsRoundedIcon />}
          href="/search?type=UNION_CLUB"
          location={location}
          theme={theme}
        />
        <DrawerItem
          open={open}
          text="중앙 동아리"
          icon={<PeopleRoundedIcon />}
          href="/search?type=CENTRAL_CLUB"
          location={location}
          theme={theme}
        />
        <DrawerItem
          open={open}
          text="과 동아리"
          icon={<PeopleOutlineRoundedIcon />}
          href="/search?type=DEPARTMENT_CLUB"
          location={location}
          theme={theme}
        />
      </List>
      {adminClubs?.length > 0 && (
        <>
          <Divider />
          <List>
            {adminClubs?.map(club => (
              <DrawerItem
                key={`${club.id} ${club.name} admin`}
                open={open}
                text={`${club.name} 운영진`}
                icon={<ClubAvatar name={club.name} />}
                badge={<LockBadge />}
                href={`/manage/${club.id}/member`}
                location={location}
                theme={theme}
              />
            ))}
          </List>
        </>
      )}
      <Divider />
      <List>
        {clubs?.map(club => (
          <DrawerItem
            key={`${club.id} ${club.name}`}
            open={open}
            text={club.name}
            icon={<ClubAvatar name={club.name} />}
            href={`/club/${club.id}`}
            location={location}
            theme={theme}
          />
        ))}
      </List>
    </Drawer>
  )
}

export default Sidebar
